import React from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { Button, Dropdown, message } from 'antd'
import mylogo from '../../homeviews/img/sun.png'
function Mynav (props) {
  let username = 0
  const users = JSON.parse(localStorage.getItem('token'))
  // 获取用户名
  if (users) {
    username = users.data.username
  } else {
    username = 0
  }
  //  登录注册修改的函数
  function showchange3 () {
    if (username !== 0) {
      return (
        <div style={{ paddingTop: '1vh' }}>
          <a
            className="underscore "
            style={{ marginRight: '2vh', color: 'rgb(94, 186, 121)' }}
            onClick={() => {
              props.history.push('/userprofile')
            }}
          >
            {username}
          </a>
          <a
            onClick={() => {
              localStorage.removeItem('token')
              props.history.replace('/home')
              message.success({
                content: '退出成功'
              })
            }}
            style={{ color: 'red' }}
            className="underscore "
          >
            退出登录
          </a>
        </div>
      )
    } else {
      return (
        <div >
          <Button type="text" onClick={() => {
            props.history.push('/login')
          }}
            > 登录</Button>
          <Button type="text" onClick={() => {
            props.history.push('/register/registerselect')
          }}>注册</Button>
        </div>
      )
    }
  }
  // 下拉菜单的items数组
  const items = [
    // nav的下拉菜单items数组
    {
      key: '1',
      label: (
        <Button type="text">警惕诈骗</Button>
      )
    }, {
      key: '2',
      label: (
        <Button type="text" onClick={() => {
          props.history.push('/tipofftelephone')
        }}>举报电话</Button>
      )
    }, {
      key: '3',
      label: (
        <Button type="text">帮助中心</Button>
      )
    }

  ]
  return (
    <div>
      <div className="Businesspage-nav">
        <div className="Businesspage-nav-left" >
          <a onClick={() => { props.history.push('/home') }}>
            <img
              src={mylogo}
              style={{ height: '4vh', width: '2vw', float: 'left' }}
            ></img>
            <span className="Businesspage-nav-left-ziti">阳光高考</span>
          </a>
          <Button type="text" onClick={() => { props.history.push('/home') }}>首页</Button>
          <Button type="text" onClick={() => { props.history.push('/consult') }}>  高考资讯</Button>
          <Button type="text" onClick={() => { props.history.push('/collegelibrary') }}>院校库</Button>
          <Button type="text" onClick={() => { props.history.push('/minquire') }}>专业库</Button>
          <Button type="text" onClick={() => { props.history.push('/ainquire') }}>录取查询</Button>
          <Button type="text" onClick={() => { props.history.push('/smatching') }}>智能匹配</Button>
          <Button type="text" onClick={() => { props.history.push('/gradethreshold') }}>历年分数线</Button>
          <Button type="text" onClick={() => { props.history.push('/regulation') }}>招生章程</Button>
          <Button type="text" onClick={() => { props.history.push('/mrecommendation') }}>专业推荐</Button>
          <Button type="text" onClick={() => { props.history.push('/minterpretation') }}>专业解读</Button>
           <Dropdown
            menu={{
              items
            }}
          >
            <a onClick={(e) => e.preventDefault()} style={{ float: 'right', margin: '1.05vh 0' }}>
            • • •
            </a>
          </Dropdown>
        </div>
        <div className="Businesspage-nav-right">
       {showchange3()}
        </div>
      </div>
    </div>
  )
}
Mynav.propTypes = {
  history: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired,
  name: PropTypes.object.isRequired
}
export default withRouter(Mynav)
